import React, { useState } from 'react';
import { Link } from 'react-router-dom';

function Navbar() {
  // El estado para controlar la visibilidad sigue siendo el mismo.
  const [isMenuVisible, setIsMenuVisible] = useState(false);

  // CLAVE: Creamos una nueva funcin que se ejecutar al hacer clic.
  // Esta funcin invierte el valor actual de 'isMenuVisible'.
  // Si est en 'false', lo cambia a 'true', y viceversa.
  const handleToggleMenu = () => {
    setIsMenuVisible(!isMenuVisible);
  };

  return (
    <nav className="categories-navbar">
      <ul className="nav-links">
        {/*
          - Eliminamos los eventos onMouseEnter y onMouseLeave.
          - Ahora, el 'li' ya no controla el men.
        */}
        <li className="dropdown">
          {/*
            - El evento 'onClick' ahora est en el enlace <a>.
            - 'event.preventDefault()' se aade para evitar que el enlace '#' intente navegar a algn sitio.
          */}
          {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
          <a href="#" onClick={(e) => { e.preventDefault(); handleToggleMenu(); }}>
            CATEGORAS 
          </a>

          {/* La lgica para aadir la clase 'visible' no cambia. Sigue dependiendo del estado. */}
          <ul className={`dropdown-menu ${isMenuVisible ? 'visible' : ''}`}>
            {/* Para que el men se cierre al hacer clic en una opcin, aadimos 'onClick' aqu tambin */}
            <li onClick={() => setIsMenuVisible(false)}><Link to="/ficcion"> Ficcin</Link></li>
            <li onClick={() => setIsMenuVisible(false)}><Link to="/no-ficcion"> No ficcin</Link></li>
            <li onClick={() => setIsMenuVisible(false)}><Link to="/ciencia-tecnologia"> Ciencia y tecnologa</Link></li>
            <li onClick={() => setIsMenuVisible(false)}><Link to="/arte-cultura"> Arte y cultura</Link></li>
            <li onClick={() => setIsMenuVisible(false)}><Link to="/negocios-economia"> Negocios y economa</Link></li>
            <li onClick={() => setIsMenuVisible(false)}><Link to="/infantil-juvenil"> Infantil y juvenil</Link></li>
          </ul>
        </li>
        <li><Link to="/">LIBROS</Link></li>
        <li><Link to="/infantil-juvenil">INFANTIL Y JUVENIL</Link></li>
      </ul>
      <hr className="nav-separator" />
    </nav>
  );
}

export default Navbar;